.container {
  height: 100vh;
}

.rows,
textarea.input-form {
  width: 40%;
  height: 500px;
  display: inline-block;
  overflow: auto;
  text-align: left;
  margin-left: 20px;
  position: relative;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
}

.rows {
  border: 1px solid #999;
  padding: 2px;
}

textarea.input-form {
  resize: none;
}


